<template>
	<view class="content">
		<view class="top_Img">
			<image :src="'data:image/jpg;base64,' + tbg" mode=""></image>
		</view>
		<view class="search" @click="searchCli">
			<search></search>
		</view>
		
		<slideTab v-on:sortid="getsortid"></slideTab>
		<bottom :sendnews='newslist'></bottom>
	</view>
</template>

<script>
	import search from '../../componets/search.vue'
	import slideTab from '../../componets/slideTab.vue'
	import bottom from '../../componets/index/newDetials.vue'
	export default {
		components:{
			search,
			slideTab,
			bottom,
		},
		data() {
			return {
				title: 'Hello',
				tbg:"",
				newslist:[],
			}
		},
		onLoad() {
			uni.setStorageSync('sortid','0');
			// 请求词云图
			uni.request({
				url:'http://122.9.13.250:8000/wordcloud',
				method:'post',
				data:{
					sortid:uni.getStorageSync('sortid')
				},
				header: {
				    'Content-Type': 'application/x-www-form-urlencoded'
				},
				dataType: 'json',
				success:(res)=>{
					let url  = ''
					let sortid = uni.getStorageSync('sortid');
					if(sortid=='0'){
						this.tbg = url;
					}else{
						this.tbg = res.data.wordcloud
					}
					// console.log(res.data.wordcloud)
					// console.log(this.tbg)
				}
			})
			// 请求新闻
			uni.request({
				url:'http://122.9.13.250:8000/get_news_digest',
				method:'post',
				data:{
					sortid:'104',
					start:'0',
					end:'4',
				},
				header: {
				    'Content-Type': 'application/x-www-form-urlencoded'
				},
				dataType: 'json',
				success:(val)=>{
					console.log(val.data.news);
					this.newslist = val.data.news;
				}
			})
		},
		methods: {
			searchCli(){
				uni.navigateTo({
					url: '../find/find',
				});
			},
			getsortid(val){
				console.log(val);
				uni.setStorageSync('sortid',val);
				// 请求词云图
				uni.request({
					url:'http://122.9.13.250:8000/wordcloud',
					method:'post',
					data:{
						sortid:uni.getStorageSync('sortid')
					},
					header: {
					    'Content-Type': 'application/x-www-form-urlencoded'
					},
					dataType: 'json',
					success:(res)=>{
						let url  = ''
						let sortid = uni.getStorageSync('sortid');
						if(sortid=='0'){
							this.tbg = url;
						}else{
							this.tbg = res.data.wordcloud
						}
					}
				})
				// 请求新闻简介
				uni.request({
					url:'http://122.9.13.250:8000/get_news_digest',
					method:'post',
					data:{
						sortid:'104',
						start:'0',
						end:'3',
					},
					header: {
					    'Content-Type': 'application/x-www-form-urlencoded'
					},
					dataType: 'json',
					success:(val)=>{
						console.log(val.data.news),
						this.newslist = val.data.news;
					}
				})
			}
		}
	}
</script>

<style scoped lang="less">
	.content{
		margin: 0;
		padding: 0;
	}
	.top_Img{
		image{
			width: 100%;
			height: 348rpx;
		}
	}
	.search{
		padding: 0 3%;
	}

</style>
